

.form__entering {
  position: absolute;
  top: 150%;
  text-align: center;
  /*left: 10%;*/
  /*transform: translate(0, -50%);*/
}


.form {
  transition: 1s;
  position: absolute;
  top: 10%;
  /*padding: auto 0;*/
  /*border: 1px solid red;*/
  text-align: center;
  /*left: 5%;*/
  /*border: 1px solid red;*/
}

.form__exiting {
  transition: 1s;
  position: absolute;
  top: 10%;
  /*left: 110%;*/
  text-align: center;
  display: none;
  /*visibility: hidden;*/
  /*left: 10%;*/
  /*transform: translate(0, -50%);*/
}

.form__title {
  color: white;
  margin-bottom: 1rem;
}

.form__description {
  color: #ddd;
  margin-bottom: 1rem;
}

.nominators__wrapper {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
}

.nominator__button {
  width: 40vw;
  margin: 2vw;
  background-color: rgba(200, 200, 200, 0.3);
  border: 1px solid #ddd;
  border-radius: 5px;
  outline: none;
  padding: 10px;
  position: relative;
  /*border-top-right-radius: 40px;*/
}

.nominator-button-clicked {
  background-color: rgba(225, 225, 225, 0.5);
  border: 1px solid #fff;
  border-radius: 5px;
}

.nominator__button-dark {
  background-color: rgba(100, 100, 100, 0.5);
  border: 1px solid #999;
  border-radius: 5px;
}

.nominator__img {
  height: 20vh;
  /*max-width: 30vw;*/
}

.nominator__img-dark {
  filter: brightness(50%);
}

.nominator__text {
  color: white;
}

.nominator__text-dark {
  color: #999;
}

.form__submit-button {
  padding: 0.75rem 2rem;
  background-color: black;
  color: white;
  font-size: 1.25rem;
  margin: 2rem auto;
  margin-bottom: 10vh;
  border: none;
  outline: none;
  border-radius: 5px;
}

.submit-button-clicked {
  background-color: #555;
  color: orange;
}

.error-text-wrapper {
  color: red;
  height: 4rem;
  width: 80vw;
  margin: 0 auto;
  margin-top: 5vh;
  /*margin-bottom: 3vh;*/
  background-color: rgba(200, 0, 0, 0.3);
  border-radius: 5px;
}

.error-text {
  padding: 1.5rem;
}